import React, { Component } from 'react'
import {signup} from "../../services/userWs"
import { Link } from "react-router-dom"

class SignupContainer extends Component {
   state = {
       data:{}
   }
    
   handleChange = (event) => {
       const { value, name } = event.target;
       let { data } = this.state;

       data[name] = value

       this.setState({ data })
   }    

   onSubmit = (event) =>{
       event.preventDefault()
       console.log("voy a enviar datos")
       const { history } = this.props
       signup(this.state.data).then((response)=>{
        this.setState({data:{}})

        history.push("/login")

       }).catch((error)=>{
            console.log('hay un error', error)
       })
   }

    render() {
        console.log('la data', this.state.data)
        const {handleChange, onSubmit} = this
        const {data} = this.state
        return (
            <section className='uk-section'>
            <div className='uk-container uk-flex uk-flex-center'>
            <div className='uk-width-1-4'>
                <h3>Regístrate</h3>
                <form onSubmit={onSubmit} className='uk-width-1-1 uk-form-stacked uk-flex uk-flex-center uk-flex-column'>
                    <div className="uk-margin">
                        <div className="uk-inline">
                            <span className="uk-form-icon" uk-icon="icon: user"></span>
                            <input className="uk-input" type="text" name="email" onChange={handleChange} value={data['email'] ? data['email'] : ""} required/>
                        </div>
                    </div>
                    <div className="uk-margin">
                        <div className="uk-inline">
                            <span className="uk-form-icon" uk-icon="icon: user"></span>
                            <input className="uk-input" type="text" name="name" onChange={handleChange} value={data['name'] ? data['name'] : ""} required/>
                        </div>
                    </div>

                    <div className="uk-margin">
                        <div className="uk-inline">
                            <span className="uk-form-icon uk-form-icon-flip" uk-icon="icon: lock"></span>
                            <input className="uk-input" type="password" name='password' onChange={handleChange} value={data['password'] ? data['password'] : ""} required/>
                        </div>
                    </div>
                    <div className="uk-margin">
                        <div className="uk-inline">
                            <span className="uk-form-icon" uk-icon="icon: lock"></span>
                            <input className="uk-input" type="password" name="confirmPassword" onChange={handleChange} value={data['confirmPassword'] ? data['confirmPassword'] : ""} required/>
                        </div>
                    </div>
                    <div className="uk-text-meta">
                        ¿Ya tienes cuenta? {" "} 
                        <Link className="uk-text-primary" to="/login">
                        Haz login aqui.
                        </Link>
                    </div>
                    <button className="uk-button uk-button-primary">Registrarme</button>
                </form>
            </div>
            

            </div>
                
                
            </section>
        )
    }
}

export default SignupContainer